const NavBar = () => { 

  return (
  

  <Navbar bg="light">
      <Container   className='Navbar'> <Navbar.Toggle aria-controls="navbarScroll" />
      <div className='navbar_left' >
      <Navbar.Brand href="#" className="nav_head">My Portfolio</Navbar.Brand>
     
        </div>
        <div className='navbar_right'>
      
        <Navbar.Collapse id="navbarScroll">
          
             
          
            <Nav >
       <Link to="About" smooth={true} duration={150}><h4>About</h4></Link>
      <Link to="Skills" smooth={true} duration={150}><h4 >Skills</h4></Link>
<Link to="Project" smooth={true} duration={150}><h4>Project</h4></Link>
      <Link to="Contact" smooth={true} duration={150}><h4>Contact</h4></Link>
</Nav>
</Navbar.Collapse>     </div>
          
        
       
      </Container>
    </Navbar>
  );
}


export default NavBar;
