
import React from 'react';

import Plx from "react-plx";
import { Element } from 'react-scroll';

import { Link } from 'react-scroll';

const Header = () => {
  return (
      <div>
          <Link to="Header" smooth={true} duration={10}> 
<Element name='Header'>
{/*  */}
          <div className='header'>
              <Plx parallaxData={[
                  {
                      start: 0,
                      duration: 1000,
                      easing:"easeInOut",
                      properties: [
                          {
                            startValue: -500,
                            endValue:900,
                            property: "translateX",
                          },
    
                      ]
                  }]}>
              
               <div className='header_left'>
                   <h1>Hello It'Me</h1>
           <span className='logo'>Godwin</span>
           <h2>I'm a Frontend Developer</h2>

                     <div className='btns'>
           {/* <a href='#' className='header_cv'> <button> Download CV </button></a> */}
           {/* <a href='#' className='header_work'> <button> My Work</button></a> */}
               </div>
              </div>
               </Plx>
              </div>

              <svg  className='Arrow' xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-up-circle" viewBox="0 0 16 16">
  <path fill-rule="evenodd" d="M1 8a7 7 0 1 0 14 0A7 7 0 0 0 1 8zm15 0A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-7.5 3.5a.5.5 0 0 1-1 0V5.707L5.354 7.854a.5.5 0 1 1-.708-.708l3-3a.5.5 0 0 1 .708 0l3 3a.5.5 0 0 1-.708.708L8.5 5.707V11.5z"/>
</svg>
             
          </Element>
          </Link> 

          </div>
   )
 }

export default Header;



  
