
import React from 'react';
import Plx from "react-plx";

import { Element  } from 'react-scroll';


const About = () => {
  return (
      <div>
          <Element name='About'> 
              
         
          <div className='About_Me'>
              <h1 className='About_header'>About <span>Me</span></h1>
              <Plx parallaxData={[
                  {
                      start: 0,
                      duration: 1000,
                      easing:"easeInOut",
                      properties: [
                          {
                            startValue: -800,
                            endValue:100,
                            property: "translateX",
                          },
    
                      ]
                  }]}>
              <div className='about_left'>
                  <h2 >I'm Godwin  <span> <br></br>And I'm a<br></br>Frontend Developer.</span></h2>

                  <p>
                      I'm a highly motivated person with a passion for the development Front-End.
You can check out my projects on my portfolio and see more web projects on my Github profile. And I'm currently improving my skills in frontend.
                  </p>

              </div>
              </Plx>              
              </div>
              </Element>
    </div>
  )
}

export default About;
